import { mapState } from 'vuex'
export default {
  middleware: ['pagePermission'],
  components: {
    ThemeColors: () =>
      import('@/components/dashboard/settings/theme/ThemeColors'),
    ThemeApp: () => import('@/components/dashboard/settings/theme/ThemeApp')
  },
  data() {
    return {
      items: [
        { title: this.$t('tabs.dashboard.settings.colors'), href: '#colors' },
        { title: this.$t('tabs.dashboard.settings.app'), href: '#app' }
      ]
    }
  },
  computed: {
    ...mapState('settings', {
      settings: (state) => state.settings
    })
  }
}
